import {getID} from '../common/tool';

export class Base {
    constructor(x, y) {
        this.x  = x;
        this.y  = y;
        this.vx = 0;
        this.vy = 0;
        this.ax = 0;
        this.ay = 0;
        this.f  = 0; // 
        this.spring = 0.5; // 
        // this.free   = true; // v/a
        this.scale  = 1; // 
        this.rotate = 0; // 
        this.fillStyle   = 'rgba(0, 0, 0, 0)'; // 
        this.strokeStyle = 'rgba(0, 0, 0, 1)'; // 
        // this.playYard  = PLAY_ZONE ; // 
        this.m = 1; // 
        this.angularVelocity = 0;
        this.dead = false; // true
        this.__GUID = getID();
    }

    move(freq = 1) {
        // 
        this.vx += this.ax / freq;
        this.vy += this.ay / freq;
        // 
        this.vx *= 1 - this.f / freq;
        this.vy *= 1 - this.f / freq;
        // 
        this.x += this.vx / freq;
        this.y += this.vy / freq;
    }

    draw() {/**/}
    getBounds() {/**/}
    destory() {this.dead = true}
    action() {/**/}
}



