/**
 * 
 * - ABarcbound
 * @method __elasticImpactArcBound
 * @param  {[type]}                A [description]
 * @param  {[type]}                B [description]
 * @return {[type]}                  [description]
 */
function useElasticCollision(A, B) {
    let ball, zone, restitution;
    let boundA = A.getBounds();
    let boundB = B.getBounds();
    if (boundA.type === 'arc') {
        ball = A;
        zone = boundB.zone;
        restitution = boundB.restitution;
    }
    else {
        ball = B;
        zone = boundA.zone;
        restitution = boundA.restitution;
    }
    if (ball.x + ball.radius >= zone[2]) {
        ball.vx = -ball.vx * restitution;
        ball.ax = -ball.ax;
        ball.x = zone[2] - ball.radius; // , 
    }
    if (ball.x - ball.radius <= zone[0]) {
        ball.vx = -ball.vx * restitution;
        ball.ax = -ball.ax;
        ball.x = zone[0] + ball.radius; // , 
    }
    if (ball.y + ball.radius >= zone[3]) {
        ball.vy = -ball.vy * restitution;
        ball.ay = -ball.ay;
        ball.y = zone[3] - ball.radius; // , 
    }
    if (ball.y - ball.radius <= zone[1]) {
        ball.vy = -ball.vy * restitution;
        ball.ay = -ball.ay;
        ball.y = zone[1] + ball.radius; // , 
    }
}

export {
    useElasticCollision,
}



