/**
 * 
 * @method getDistance
 * @param  {{x,y}}    A
 * @param  {{x,y}}    B
 * @return {Float}    
 */
function getDistance(A, B) {
    return Math.sqrt(Math.pow(A.x - B.x, 2) + Math.pow(A.y - B.y, 2));
}

/**
 * 
 */
class Vec2 {
    constructor(x, y) {
        this.x = x;
        this.y = y;
        this.info = {}; // , diao
    }
    // 
    getMod() {
        return Math.sqrt(Math.pow(this.x, 2) + Math.pow(this.y, 2));
    }
    // 
    getOpp() {
        return new Vec2(-this.x, -this.y);
    }
    //  ()
    getPerp() {
        return new Vec2(this.y, -this.x);
    }
    // 
    product(n) {
        return new Vec2(this.x * n, this.y * n);
    }
    mult(n) {
        return new Vec2(this.x * n, this.y * n);
    }
    // 
    dot(b) {
        return this.x * b.x + this.y * b.y;
    }
    // 
    perpDot(b) {
        let res = this.getOpp().dot(b);
        // 
        if (res < 0) {
            res = -res;
        }
        return res
    }
    //  
    add(b) {
        return new Vec2(this.x + b.x, this.y + b.y);
    }
    // 
    sub(b) {
        return new Vec2(this.x - b.x, this.y - b.y);
    }
    // 
    equal(b) {
        return this.x === b.x && this.y && b.y;
    }
    // 
    set(x, y) {
        this.x = x;
        this.y = y;
    }
    // 
    unit() {
        let mod = this.getMod();
        // console.log('mod', mod);
        if (mod === 0) {
            throw new Error(': ', JSON.stringify(this));
        }
        return this.mult(1/mod);
    }
    // 
    isZero() {
        return this.x === 0 && this.y === 0;
    }
    // 
    rotate(deg, o) {
        let len = this.sub(o).getMod();
        // console.log(len, this.x, this.y);
        let  = Math.atan2(this.y - o.y, this.x - o.x);
        let  =  + deg;
        // console.log(, len * Math.cos(), len * Math.sin());
        this.x = o.x + len * Math.cos();
        this.y = o.y + len * Math.sin();
    }
}

// 
function vecTripleProduct (a, b, c) {
    // a * b * c = -a(c . b) + b(c . a)
    // console.log('', a, b, c, b.product(c.dot(a)).sub(a.product(c.dot(b))));
    // console.log('#001', c.dot(a), b.product(c.dot(a)), c.dot(b), a.product(c.dot(b)));
    return b.mult(c.dot(a)).sub(a.mult(c.dot(b)));
}

// 
function support(shapeA, shapeB, dir) {
    let pA = shapeA.getFarthest(dir);
    let pB = shapeB.getFarthest(dir.getOpp());
    // console.log('support: ', dir, pA, pB, pA.sub(pB));
    let support = pA.sub(pB);
    support.info.pair = {
        A: pA,
        B: pB,
    }
    return support;
}

// 
function getDisPointLine(p, a, b) {
    // 
    let ab = b.sub(a);
    // 
    let pa = a.sub(p);
    // 
    let n = vecTripleProduct(ab, pa, ab);
    if (n.isZero()) {return 0};
    n = n.unit();
    // 
    let d = n.dot(a);
    return d;
}

export {
    getDistance,
    Vec2,
    vecTripleProduct,
    support,
    getDisPointLine,
};

