import { getDistance, Vec2, vecTripleProduct, support } from '../../common/math';

import GJK from './GJK';
import EPA from './EPA';


// ,
// 
function contactHandler(contact) {
    let Avx = contact.shapeA.vx; // A 
    let Avy = contact.shapeA.vy; // A 
    let Bvx = contact.shapeB.vx; // B 
    let Bvy = contact.shapeB.vy; // B 
    let Aav = contact.shapeA.angularVelocity; // A
    let Bav = contact.shapeB.angularVelocity; // B
    let n = contact.normal; // ()()(BA)
    // let depth = contact.depth; // ()
    // , 
    if (contact.featureA.type === 'point' || contact.featureB.type === 'point') { // : 
        // 
        const A = contact.shapeA;
        const B = contact.shapeB;
        const vA1 = new Vec2(A.vx, A.vy);
        const vB1 = new Vec2(B.vx, B.vy);
        // 
        const P = contact.featureA.type === 'point' ? contact.featureA.point : contact.featureB.point;
         // 
        const e = 0.8;
        // 
        const mA = contact.shapeA.m;
        const mB = contact.shapeB.m;
        // r
        const rAP = P.sub(A.centroid);
        const rBP = P.sub(B.centroid);
        // : vP = vO + *rOP
        const vAP = vA1.add(rAP.getPerp().mult(A.angularVelocity)); // (y)
        const vBP = vB1.add(rBP.getPerp().mult(B.angularVelocity)); // (y)
        const vAB = vAP.sub(vBP);
        // 
        const jUP = -(1+e) * vAB.dot(n);
        // 
        const jDOWN = (n.dot(n) * (1/mA + 1/mB)) + Math.pow(rAP.perpDot(n), 2)/A.I + Math.pow(rBP.perpDot(n), 2)/B.I;
        // 
        const j = jUP/jDOWN;
        // 
        const pA = n.mult(j).getOpp();
        const pB = n.mult(j);
        // 
        const vA2 = vA1.add(pA.mult(1/mA));
        const vB2 = vB1.add(pB.mult(1/mB));
        A.vx = vA2.x;
        A.vy = vA2.y;
        B.vx = vB2.x;
        B.vy = vB2.y;
        let deltaOmigaA = rAP.perpDot(pA) / A.I;
        let deltaOmigaB = rBP.perpDot(pB) / B.I;
        A.angularVelocity += deltaOmigaA;
        B.angularVelocity += deltaOmigaB;
        console.log('#253, done');
    }
}

